

/* === Waveform === */

.waveform-bar {
  position: absolute;
  bottom: 0.5rem;
  left: 0;
  width: 100%;
  padding: 0 0.5rem;
  pointer-events: none;

  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin: 1rem auto 2rem;
  max-width: 420px;
  height: 28px;
  opacity: 0.9;
  position: relative;
  z-index: 2;
}

.waveform-bar .bar {
  display: inline-block;
  width: 2px;
  height: 16px;
  margin: 0 1px;
  background-color: #f1c40f;
  animation: bounce 1s infinite ease-in-out;
  animation-play-state: paused;  
  transform: scaleY(0.4); 

}

.waveform-bar.playing .bar {
  animation-play-state: running;
}

.track-card.playing .bar {
  animation-play-state: running;
}



.waveform-bar .bar:nth-child(2) { animation-delay: 0.1s; }
.waveform-bar .bar:nth-child(3) { animation-delay: 0.2s; }
.waveform-bar .bar:nth-child(4) { animation-delay: 0.3s; }
.waveform-bar .bar:nth-child(5) { animation-delay: 0.4s; }


.waveform-mini {
  position: absolute;
  bottom: 0.5rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 2px;
  height: 12px;
  opacity: 0.8;
}

.waveform-mini .bar {
  width: 2px;
  height: 100%;
  background-color: #f1c40f;
  animation: bounce 1s infinite ease-in-out;
  animation-play-state: paused;
  transform-origin: bottom;
}


@keyframes bounce {
  0%, 100% { transform: scaleY(0.4); }
  50% { transform: scaleY(1); }
}
